import React from 'react';
import hero from './images/Hero.jpg'
import './App.css';
import Properties from './components/Properties/Properties.js';
import Amenities from './Amenities.js';
import ResidentPortal from './ResidentPortal.js';
import Nav from './components/navigation.js';
import {BrowserRouter as Router, Switch, Route } from 'react-router-dom';
function App() {
  return (
    <Router>
      <div className="App">
          <Nav/>
          <Switch>
            <Route path ="/LatinConnection/" exact component = {Home}/>
            <Route path="/LatinConnection/Events" component = {Properties}/>
            <Route path="/LatinConnection/Gallery" component = {Amenities}/>
            <Route path = "/LatinConnection/Contact" component = {ResidentPortal}/>

          </Switch>
      </div>
    </Router>
  );
}
const Home = () => {
  return(
    <div className = "homepage">
      <div className = "landing-image" style = {{background:`url(${hero}) center no-repeat` , width: "100vw", height: "45vh", backgroundSize: "cover"} }> </div>
      <h1> Home </h1>
      <div className = "Features">
        <div className ="Tsection"></div>
        <div className ="Tsection"></div>
        <div className ="Tsection"></div>
      </div>
    </div>
  )
}

export default App;
