import React, {useState} from 'react';
import './navigation.css';
import {Link} from 'react-router-dom';
import email from '../images/email.png'

export default function Nav(){
  const [opened, setOpen] = useState(false);


  function Hamburger() {

    return (
      <div className = "Hamburger" onClick = {() => setOpen(opened => !opened)}>
        <div className = "top">  </div>
        <div className = "middle"> </div>
        <div className = "bottom"> </div>
      </div>
    )
  };

  function ExitHamburger() {
    return(
      <div className = "Exit Hamburger" onClick = {() => setOpen(opened => !opened)}>
         <div className = "top">  </div>
         <div className = "middle"> </div>
         <div className = "bottom"> </div>
       </div>
    )
  };

  function Open(){
    return(
      <div className = "OpenNavigation">
        <Link to="/LatinConnection/"   className = "Menu MenuItem1">
          <div   onClick = {() => setOpen(opened => !opened)}> Home </div>
        </Link>
        <Link to="/LatinConnection/Gallery" className = "Menu MenuItem2">
          <div    onClick = {() => setOpen(opened => !opened)}> Gallery </div>
        </Link>
        <Link to="/LatinConnection/Events" className  = "Menu MenuItem3">
          <div    onClick = {() => setOpen(opened => !opened)}> Events </div>
        </Link>
        <Link to ="/LatinConnection/Contact"  className = "Menu MenuItem4">
          <div    onClick = {() => setOpen(opened => !opened)}> Contact </div>
        </Link>
      </div>
    )
  };

  if (opened) {
  return(
    <div className = "Navigation">
      <h2 className = "logo">Latin Connection</h2>
      <ExitHamburger/>
    <Open/>
    </div>

   )
 };
  return(
    <div className = "Navigation">
    <Link to="/" className = 'logo' style={{ textDecoration: 'none' }}>
      <h2>Latin Connection</h2>
    </Link>
    <div className = "Desktop">
      <div className = "email"> <img className = 'email-icon' src = {email}/>
      </div>
    </div>
      <Hamburger/>
    </div>
  )
}
